<template>
    <div class="sDingdan">
      <router-link tag="div" :to="{'path':'/addressList','query':{choose:'true',name:addInfo.name,cellphone:addInfo.cellphone,id:addInfo.id,address:addInfo.address}}" class="address">
        <div v-if="addInfo.id">
          <p>配送地址</p>
          <p>{{addInfo.address}}</p>
          <p>{{addInfo.name}}</p>
          <p>{{addInfo.cellphone}}</p>
        </div>
        <div v-else>
          <p>请选择地址</p>
        </div>
        
        <img src="../assets/img/querendingdan--huoche.png" alt="">
        <div class="iconfont icon-yousanjiao1 youbian"></div>
      </router-link>
      <div class="buygood">
        <div class="secone">
          <img src="../assets/img/timg.jpg" alt="">
          <div class="gdetail">
            <h3>{{title}}</h3><!--
            <div  class="cnashu"><input type="file">黄色/1.74</div>-->
            <div class="jifen">{{jifen}}*{{num}}</div>
          </div>
        </div>
        <div class="sectwo">
          <span>数量</span>
          <van-stepper :max="goodsInfo.goods_inventory" v-model="num" />
        </div>
        <div class="secthree">
          <div>留言</div>
          <textarea name="" v-model="buyer_message" id="liuyan" cols="30" rows="10" placeholder="请输入您的留言"></textarea>
        </div>
      </div>
      <div class="Sbottom">
        <div class="count">合计: <span>{{count}}</span>积分</div>
        <van-button type="default" class="duihuan" @click="toChange">兑换</van-button>
      </div>
      <a href="" class="kefu iconfont icon-kefu"></a>
    </div>
</template>

<script>
import { getStore, removeStore } from "../assets/utils.js";
import { Toast } from "vant";
import { apiGet, apiPost } from '../common/js/api.js';
    export default {
        name: "sure_dingdan",
      data(){
          return{
            params:this.$route.params,
            buyer_message:"",//买家留言
            value:'',
            jifen:'',
            title:'',
            num:'',
            addInfo:{
              // addressName:'四川省成都市高新区天府二街蜀都中心22号',
              // addressId:1,
              // name:"王豆豆",
              // mobile:15880215644
            },
            goodsInfo:{}
          }
      },
      beforeRouteLeave:(to, from, next) => {
        if(to.name==="spXiangqin"){
          removeStore("chooseAdd")
        }
        next()
      },
      created(){
        this.getOrderInfo()
      },
      mounted(){
          console.log(this.$route.params);
          let{ jifen ,title, num}=this.$route.params;
          this.jifen=jifen;
          this.title=title;
          this.num=num
      },
      computed:{
        count:function () {
          return parseInt(this.jifen)*parseInt(this.num)
        }
      },
      methods:{
        //获取订单默认地址，所需积分等信息
        getOrderInfo(){
          let formData = new FormData()
          formData.append('accessToken',this.$cookie.get('accessToken'))
          formData.append('num',1)
          formData.append('goods_id',this.params.id)
          apiPost('/goods/confirm-order',formData).then(res=>{
            if(getStore("chooseAdd")){
              this.addInfo = getStore("chooseAdd")
            }else{
              this.addInfo = res.data.data.address;
            }
            this.goodsInfo = res.data.data.goods.goods_info
          })
        },
        toChange(){
          if(!this.addInfo.id){
            Toast("请选择地址");
            return 
          }
          let formData = new FormData()
          formData.append('accessToken',this.$cookie.get('accessToken'))
          formData.append('num',this.num)
          formData.append('goods_id',this.params.id)
          formData.append('address_id',this.addInfo.id)
          formData.append('buyer_message',this.buyer_message)
          apiPost('/goods/add-order',formData).then(res=>{
            this.$router.push({
              name:'sPay',
              query:{
                order_id:res.data.data.order_id,
                total_integral :res.data.data.total_integral
              }
            })
          })
          
        }
      }
    }
</script>

<style  lang="scss">
  .sDingdan{
    height: 100%;
    background: #E6E9ED;
    padding-top: 0.18rem;
    .address{
      width: 3.5rem;
      background:linear-gradient(-90deg,rgba(127,173,249,1),rgba(70,133,238,1));
      border-radius:0.04rem;
      margin: 0 auto 0.17rem;
      position: relative;
      padding:0.25rem 0.12rem ;
      p{
        color: rgba(255,255,255,.8);
        font-size: 0.15rem;
        line-height: 0.24rem;
        &:nth-of-type(1){
          font-size: 0.17rem;
        }
      }
      img{
        position: absolute;
        bottom: 0.1rem;
        width: 0.72rem;
        right: 0.3rem;
      }
      .youbian{
        width: 0.09rem;
        height: 0.17rem;
        color: white;
        position: absolute;
        right: 0.12rem;
        top:43%;
      }
    }
    .buygood{
      background: white;
      width: 3.5rem;
      height:4.7rem;
      padding: 0.21rem 0.13rem 0.17rem;
      margin:  0.17rem auto 0.7rem;
      border-radius: 0.04rem;
      .secone{
        display: flex;
        border-bottom: 1px solid #F5F5F5;
        padding-bottom: 0.21rem;
        img{
          width: 0.73rem;
          height: 0.73rem;
          border-radius: 0.04rem;
        }
        .gdetail{
          flex: 1;
          padding: 0.02rem 0.13rem;
          h3{
            font-weight: 600;
            font-size: 0.16rem;
          }
          div{
            color: #9a9a9a;
          }
        }
      }
      .sectwo{
        height: 0.5rem;
        line-height: 0.5rem;
        border-bottom: 1px solid #F5F5F5;
        span{
          font-size: 0.17rem;
          color: #333;
        }
        .van-stepper{
          float: right;
        }
        .van-stepper__input{
          border: none;
        }
      }
      .secthree{
        div{
          font-size:0.16rem;
          color: #333;
          line-height: 0.47rem;
        }
        #liuyan{
          width: 3.25rem;
          border-radius: 0.04rem;
          background: #E5E8EB;
          border:none;
          padding: 0.13rem;
        }
      }
    }
    .Sbottom{
      height: 0.47rem;
      width: 100%;
      font-size: 0.18rem;
      position: fixed;
      bottom: 0;
      display: flex;
      .count{
        background: white;
        flex: 1;
        text-align: center;
        line-height: 0.47rem;
        span{
          color: #CC1C1C;
        }
      }
      .duihuan{
        font-size: 0.18rem;
        flex: 1;
        height: 0.47rem;
        background:linear-gradient(-90deg,rgba(60,127,238,1),rgba(103,154,237,1));
        color: white;
      }
    }
    .kefu{
      width: 0.52rem;
      height: 0.52rem;
      background:linear-gradient(-90deg,rgba(70,133,238,1),rgba(155,193,255,1));
      border-radius:50%;
      box-shadow:0px 5px 10px 0px rgba(60,127,238,0.42);
      position: fixed;
      bottom: 1.5rem;
      display: block;
      text-align: center;
      line-height: 0.52rem;
      font-size: 0.28rem;
      color: white;
      z-index: 3;
      right: 0.11rem;
    }
  }
</style>



